.bg {
  background-color: black;
}

.Introduction {
  margin: 0 auto;
  max-width: 1400px;
  text-align: center;
  background-color: bisque;
  padding-bottom: 2em;
}

.appBar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: bisque;
}

.appBar img {
  margin-top: -2em;
  height: 150px;
}

.links {
  display: flex;
  width: 250px;
  justify-content: space-evenly;
  background-color: rebeccapurple;
}

.weMajor select {
  margin: 1em 0;
  text-align: center;
}

#majorSelect {
  align-items: baseline;
  margin: 0 auto;
  display: flex;
  max-width: 100px;
}

.secSelect {
  align-items: baseline;
  margin: 0 auto;
  display: flex;
  max-width: 100px;
}

.contento {
  display: flex;
}

.text {
  width: 45%;
  font-size: 18px;
  padding: 0 3em;
  background-color: crimson;
}

.carousel {
  background-color: aqua;
  position: relative;
  width: 48%;
  font-size: 20px;
}

.carouselInner img {
  padding-top: 1em;
  width: 580px;
  height: 400px;
}

#prev,
#next {
  position: absolute;
  top: 40%;
  font-size: 2rem;
  color: black;
  text-decoration: none;
  background-color: rgb(0, 0, 0, 0.5);
  padding: 0.5rem;
  transition: all 0.3s ease-in-out;
}

#prev:hover,
#next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

#prev {
  left: 0;
}

#next {
  right: 0;
}
